@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

body {
  font-size: 16px;
  font-family: Arial;
  background-image: url("../img/bodyBg.png");
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background-color: #666;
}

body::-webkit-scrollbar-thumb {
  background-color: #ccc;
}

.container {
  max-width: 1300px;
  padding: 10px;
  margin: 0 auto;
}

.swiper-button-next,
.swiper-button-prev {
  background: #226f93c4;
  background-repeat: no-repeat;
  background-size: 25px 35px;
  background-position: center center;
  min-width: 55px;
  min-height: 55px;
  opacity: 0;
}

.swiper-button-next {
  right: 0px;
}

.swiper-button-prev {
  left: 0px;
}

header {
  background-size: cover;
  min-height: 386px;
  background-position: center center;
}

header .header {
  padding: 30px 10px;
  position: relative;
}

header .header .header_banner_text {
  color: #fff;
}

header .header .header_banner_text h1,
header .header .header_banner_text h2 {
  font-weight: normal;
  text-shadow: 2px 1px 3px #000;
}

header .header .header_banner_text h1 {
  font-size: 24px;
}

header .header .header_banner_text h2 {
  margin-top: 20px;
  font-size: 30px;
}

header .header .header_tab {
  position: absolute;
  top: 200px;
  left: 10px;
  right: 10px;
  border-radius: 8px;
  overflow: hidden;
}

header .header .header_tab .tab_btns {
  display: flex;
  justify-content: space-between;
}

header .header .header_tab .tab_btns .tab_btn {
  flex-basis: calc(100%/4);
  text-align: center;
  padding: 10px;
  text-transform: uppercase;
  background: rgba(49, 85, 108, 0.8);
  color: #fff;
  cursor: pointer;
}

header .header .header_tab .tab_btns .tab_btn.active_tab {
  background: #E8803E;
}

header .header .header_tab .tab_bodys .tab_body {
  display: none;
  background: #fff;
  padding: 15px;
}

header .header .header_tab .tab_bodys .tab_body.active_tab_body {
  display: block;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections {
  display: flex;
  justify-content: space-between;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item {
  flex-basis: 20%;
  border-right: 2px solid #eee;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item:last-child {
  border-right: none;
  flex-basis: 30%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item h3 {
  font-weight: normal;
  margin-bottom: 15px;
  text-transform: capitalize;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item label {
  display: block;
  margin-bottom: 8px;
  cursor: pointer;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item label input {
  margin-right: 8px;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item select,
header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item input {
  margin-bottom: 8px;
  padding: 5px;
  cursor: pointer;
  background: rgba(23, 99, 222, 0.1);
  border: 1px solid rgba(23, 99, 222, 0.2);
  border-radius: 5px;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item select {
  display: block;
  width: 90%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter {
  display: flex;
  justify-content: space-between;
  flex-basis: 100%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter .price_filter_inputs {
  display: flex;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter .price_filter_inputs input {
  width: 48%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .price_filter select {
  width: 30%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter,
header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter {
  display: flex;
  align-items: center;
  min-height: 35px;
  flex-basis: 48%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter input,
header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter input {
  width: 45%;
  font-size: 15px;
  margin-bottom: 0px;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .sq_filter span,
header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .floor_filter span {
  font-size: 12px;
  margin-right: 5px;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  width: 100%;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code input {
  max-width: 150px;
  height: 100%;
  margin-bottom: 0px !important;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code label {
  font-size: 14px;
  margin-right: 0px;
  margin-bottom: 0px;
}

header .header .header_tab .tab_bodys .tab_body .tab_body_sections .tab_body_sections__item .tbsi_wrap .code label input {
  max-width: unset !important;
  width: unset !important;
  margin: unset;
  height: unset;
}

header .header .header_tab .tab_bodys .tab_body button {
  display: block;
  padding: 12px 30px;
  background: #49C7EB;
  border: 1px solid #3eb7d8;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  margin: 0px 0px 0px auto;
  border-radius: 8px;
  transition: 0.5s;
}

header .header .header_tab .tab_bodys .tab_body button:hover {
  background: #29afd4;
}

.container {
  max-width: 1300px;
  padding: 10px;
  margin: 0 auto;
}

.swiper-button-next,
.swiper-button-prev {
