#slider {
  background-image: url("../img/ms.jpg");
  min-height: 450px;
  padding-top: 120px;
  padding-bottom: 20px;
}

#slider .container .swiper-container .swiper-wrapper {
  display: flex;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide {
  border-radius: 15px;
  overflow: hidden;
  background: #fff;
  padding: 7px;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_cover {
  display: block;
  max-height: 230px;
  overflow: hidden;
  position: relative;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_cover .urgent {
  position: absolute;
  top: 7px;
  left: 7px;
  background: #d00;
  padding: 5px 8px;
  color: #fff;
  border-radius: 8px;
  font-size: 14px;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_cover img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_title {
  margin: 15px 0px;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_title a {
  color: #1763DE;
  text-decoration: none;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide .item_price {
  font-weight: 600;
  margin-bottom: 10px;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide table {
  display: block;
  border-collapse: collapse;
  font-size: 14px;
}

#slider .container .swiper-container .swiper-wrapper .swiper-slide table tr td {
  padding-right: 12px;
}

#slider .container .swiper-container:hover .swiper-button-next,
#slider .container .swiper-container:hover .swiper-button-prev {
  transition: 0.5s;
  opacity: 1 !important;
}

#slider .container .see_more {
  display: block;
  margin-top: 15px;
  text-decoration: none;
  color: #fff;
  background: rgba(255, 255, 255, 0.3);
  width: fit-content;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 15px;
  transition: 0.5s;
}

#slider .container .see_more:hover {
  background: #29afd4;
}

